import * as React from "react";
import SyntaxHighlighter from 'react-syntax-highlighter';
import { atomOneDark, atomOneLight } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import { FunctionComponent, useContext, useRef } from "react";
import { State } from "../State";
import { evaIconsFill } from "./IconsLibs/iconsEvaFill";
import Icon from "./Icon";

interface SyntaxProps {
    codeString: string,
    className?: string,
    lang?: string,
    showLineNum?: boolean
}

const SyntaxHighlight: FunctionComponent<SyntaxProps> = ( { codeString, lang = "javascript", showLineNum, className } ) => {
    const { theme } = useContext( State );
    const inputEl = useRef( null );

    function copyT( txt ) {
        const cb = inputEl.current;
        cb.value = txt;
        cb.style.display = 'block';
        cb.select();
        document.execCommand( 'copy' );
        cb.style.display = 'none';
    }

    return (
        <div className={ `mb--md code-block relative ${className ? className : ""}` }>
            <button className={ "sticky round p--xs small copy-to-clipboard" } onClick={ () => copyT( codeString ) }><Icon title={ "Copy to clipboard" } className={ "fill--text" } height={ 15 } icon={ evaIconsFill.copy }/></button>
            <SyntaxHighlighter language={ lang } style={ theme === "dark" ? atomOneDark : atomOneLight } customStyle={ { padding: 15 } } showLineNumbers={ showLineNum }>
                { codeString }
            </SyntaxHighlighter>
            <textarea readOnly value={ codeString } hidden ref={ inputEl }/>

        </div>
    );
};
export default SyntaxHighlight
